import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';
import { billers } from '../../menus';

@IonicPage()
@Component({
  selector: 'page-utilitypayment',
  templateUrl: 'utilitypayment.html',
})
export class UtilitypaymentPage {

  utilities: any = [];
  langs: any;
  constructor(private clientdata: ClientdataProvider, public navCtrl: NavController, public globalVars: GlobalVarsProvider,
    public alertService: AlertServiceProvider, public navParams: NavParams) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );  
    this.utilities =  billers;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UtilitypaymentPage');
  }

  goHome() {
    this.navCtrl.setRoot('HomePage');
  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }
  openPage( event) {
    var thatt = {};
    thatt = {
      paymento: 1, utility: event
    }
    this.navCtrl.push('BillsPage', {
      title: thatt
    } );
  }

  getItems(ev: any) {
    this.utilities;
    let val = ev.target.value;
    if (val && val.trim() != '') {
      this.utilities = this.utilities.filter((utility) => {
        return (utility.title.toLowerCase().indexOf(val.toLowerCase()) > -1);
      }) }
    else{
      this.utilities = this.globalVars.allData.billers;
    }
  }
}



